<template>
  <div class="detail">
    <van-nav-bar
      title="门店信息"
      left-text="返回"
      left-arrow
      @click-left="$toPage(-1)"
    />
    <van-cell-group>
      <van-cell title="药店名称" :value="storeInfo.company" size="large" />
      <van-cell title="注册手机" :value="storeInfo.phone" size="large" />
      <van-cell title="地区" :value="storeInfo.province +' '+ storeInfo.city +' '+ storeInfo.region" size="large" />
      <!-- <van-cell title="市" :value="storeInfo.city" size="large" />
      <van-cell title="区" :value="storeInfo.region" size="large" /> -->
      <van-cell title="收货地址" :value="storeInfo.address" size="large" />
      <van-cell title="注册时间" :value="storeInfo.createdate + ' ' + storeInfo.createtime" size="large" />
      <van-cell title="客服专员" :value="storeInfo.cursorName" size="large" />
      <van-cell title="药店状态" :value="formatState(storeInfo.status)" size="large" />
      <van-cell title="是否签控销协议" :value="storeInfo.control > 0 ? '是' : '否'" size="large" />
    </van-cell-group>
    <van-button type="danger" block @click="toModify">修改证件信息</van-button>
  </div>
</template>
<script>
export default {
  data() {
    return {
      storeInfo: {}
    }
  },
  mounted() {
    this.storeInfo = JSON.parse(sessionStorage.getItem('storeInfo'))
  },
  methods: {
    onClickLeft() {},
    toModify() {
      this.$router.push({
        path: "/modifyInfo",
        query: {
          compid: this.storeInfo.companyId
        }
      });
    },
    formatState(status) {
      let text = "";
      if((status & 128) > 0){
        text = '待审核'
      } else if((status & 256) > 0) {
        text = '审核通过'
      } else if((status & 512) > 0) {
        text = '不通过'
      }
      return text;
    }

  }
};
</script>
<style scoped>
.detail .van-button {
  position: absolute;
  bottom: 30px;
  left: 20px;
  width: 710px;
  border-radius: 10px;
}
</style>
